.section {
  max-width: 750px;
  background-color: white;
  margin: auto;
  margin-bottom: 20px;
  padding: 10px 0px 10px 10px;
}

.section--top {
  max-width: 750px;
  margin: 0px auto;
}

.section__header {
  border-bottom: 2px solid #eeeeee;
  padding: 20px;
  margin: 0;
}

.section__header--display {
  display: grid;
  grid-template-columns: 1fr auto auto;
  grid-gap: 20px;
  border-bottom: 2px solid #eeeeee;
  padding: 20px;
}

@media (max-width: 767px) {
  .section__header--display {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
}